.background {
    height: 100vh;
    background-image: url("../images/mobileBackground.jpeg");
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    
    align-items: center;
    /* justify-content: center; */
    font-size:1rem;
    flex-direction: column;
    text-align: center;
    padding-top: 40%;
}


@media (min-width: 768px) {
    .background {
        height: 100vh;
        background-image: url("../images/brycenational.jpeg");
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size:1rem;
        flex-direction: column;
        padding-top: 0%;
    }
}

.aboutButton {
    color: #060b26 !important;
	/* text-transform: uppercase; */
	background: #ffffff;
	padding: 20px;
	border: 4px solid #060b26 !important;
	border-radius: 50px;
	display: inline-block;
	transition: all 0.3s ease 0s;
    width: 200px;
    font-size: 24px;
    opacity: .6;
   

}
.aboutButton:hover {
    color: #060b26 !important;
	background: #A9A9A9;
	border-color: #060b26 !important;
	transition: all 0.4s ease 0s;
    cursor: pointer;

}
.aboutButton:disabled {
    cursor: default;
    opacity: 0.7;
}