.aboutMe {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0 auto;
  width: 60%;
  max-width: 750px;
  margin-top: 10px;
}

.quotes {
  line-height: 2em;
}

.aboutMeParagraph {
  font-size: 22px;
  display: flex;
  text-align: left;
  line-height: 2em;
  margin-bottom: 1em;
}
.title {
  font-size: 50px;
}
