@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato',sans-serif;
}

.portfolio {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}
.navbar {
    background-color: #060b26;
    height: 80px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.menu-bars{
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
}

.nav-menu {
    background-color: #060b26;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
    z-index: 2;
}

.nav-menu.active {
    left: 0;
    transition: 350ms;
}

.nav-text {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
}

.nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}

.nav-text a:hover {
    background-color: #1a83ff;

}

.nav-menu-items {
    width: 100%;
}

.navbar-toggle {
    background-color: #060b26;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.navTitle {
    margin-left: 16px;
}
.background {
    height: 100vh;
    background-image: url(/static/media/mobileBackground.bb602570.jpeg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    
    align-items: center;
    /* justify-content: center; */
    font-size:1rem;
    flex-direction: column;
    text-align: center;
    padding-top: 40%;
}


@media (min-width: 768px) {
    .background {
        height: 100vh;
        background-image: url(/static/media/brycenational.c0fcba2d.jpeg);
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size:1rem;
        flex-direction: column;
        padding-top: 0%;
    }
}

.aboutButton {
    color: #060b26 !important;
	/* text-transform: uppercase; */
	background: #ffffff;
	padding: 20px;
	border: 4px solid #060b26 !important;
	border-radius: 50px;
	display: inline-block;
	transition: all 0.3s ease 0s;
    width: 200px;
    font-size: 24px;
    opacity: .6;
   

}
.aboutButton:hover {
    color: #060b26 !important;
	background: #A9A9A9;
	border-color: #060b26 !important;
	transition: all 0.4s ease 0s;
    cursor: pointer;

}
.aboutButton:disabled {
    cursor: default;
    opacity: 0.7;
}
.recipes {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 0 auto;
    width: 90%;
    max-width: 750px;
    margin-top: 10px;
    text-align: center;
}

@media (min-width: 768px) {
    .recipes {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        margin: 0 auto;
        width: 60%;
        max-width: 750px;
        margin-top: 10px;
    }
}
.storyBehindLink, storyBehindLink:visited {
    color: black;
    text-decoration: underline;
}

.storyBehindLink:hover {
    color: #DAA520;
    text-decoration: underline;
}
.ulListRecipe{
    list-style: none;
    width: 60%;
}
.listRecipes:hover {
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.15);
    transition: box-shadow 0.3s ease-in-out;
    cursor: pointer;
    background-color: #DAA520;
}
.listRecipes {
    color: black;
    box-shadow: 0.25rem 0.25rem 0.6rem rgba(0,0,0,0.5), 0 0.5rem 1.125rem rgba(75,0,0,0.5);
    margin-top: 1rem;
    min-height: 3rem;
    padding: 1rem 1rem 1rem 1rem;
    position: relative;
    font-size: 20px;
    display: flex;
    justify-content: center;
}


.listRecipesLink, listRecipesLink:visited, listRecipesLink:active {
    color: black;
    text-decoration: none;
}


.travel{
    display: flex;
}


.travelByCity {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0 auto;
  width: 60%;
  max-width: 750px;
}

.info {
  display: flex;
  text-align: left;
  line-height: 2em;
}

img {
  padding: 10px 0px;
  max-width: 100%;
  max-height: 10%;
}

h1 {
  padding-bottom: 10px;
}

h2 {
  padding-bottom: 10px;
}

.recipeInfo {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 0 auto;
    width: 90%;
    max-width: 750px;
    margin-top: 10px;
    background-color: #fff;
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 1rem;
}
@media (min-width: 768px) {
    .recipeInfo {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        margin: 0 auto;
        width: 65%;
        max-width: 750px;
        margin-top: 10px;
        background-color: #fff;
        padding-left: 10px;
        padding-right: 10px;
        border-radius: 1rem;
    }
}
.test {
    background-color: #fff;
    display: flex;
}
.content {
    margin-bottom: 30px;
}
.steps {
    counter-reset: count;
    list-style: none;
    margin: 1.75rem 0;
    padding-left: 1rem;
    width: 100%;
}
.ingredients {
        font-size: 17px;
    letter-spacing: .5px;
    line-height: 28px;
}

.origin {
    margin: 5px 0px;
    font-style: italic;
}
.step {
    background: white;
    border-radius: 0 0.5rem 0.5rem 0.5rem;
    box-shadow: 0.25rem 0.25rem 0.6rem rgba(0,0,0,0.05), 0 0.5rem 1.125rem rgba(75,0,0,0.05);
    counter-increment: gradient-counter;
    margin-top: 1rem;
    min-height: 3rem;
    padding: 1rem 1rem 1rem 3rem;
    position: relative;
    font-size: 17px;
}
.step::before {
    counter-increment: count;
    content: counters(count, ".");
    background: #DAA520;
    border-radius: 1rem 1rem 0 1rem;
    height: 3rem;
    left: -1rem;
    overflow: hidden;
    position: absolute;
    top: -1rem;
    width: 3rem;
    align-items: flex-end;
    color: black;
    display: flex;
    font: 900 1.5em/1 'Montserrat';
    justify-content: flex-end;
    padding: 0.125em 0.25em;
    z-index: 1;
   
}
.recipeInfoLink, recipeInfoLink:visited {
    color: black;
    text-decoration: underline;
}
.recipeInfoLink:hover {
    color: #DAA520;
    text-decoration: underline;
}
.aboutMe {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 0 auto;
  width: 60%;
  max-width: 750px;
  margin-top: 10px;
}

.quotes {
  line-height: 2em;
}

.aboutMeParagraph {
  font-size: 22px;
  display: flex;
  text-align: left;
  line-height: 2em;
  margin-bottom: 1em;
}
.title {
  font-size: 50px;
}

/* this is mobile first */
.mainContent {
    margin: 0 auto;
    width: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin-top: 10px;
}
.message {
    display: flex;
    justify-content: center;
    /* align-items: center; */
    flex-direction: column;
    margin: 0 auto;
    width: 85%;
    max-width: 750px;
    box-shadow: 0.25rem 0.25rem 0.6rem rgba(0,0,0,0.05), 0 0.5rem 1.125rem rgba(75,0,0,0.05);
    border-radius: 2rem;
    font-size: 18px;
    padding: 2%;
    font-family: cursive;

}
.infoRight {
    margin-left: 75%;
    font-family: cursive;

}
.messageParagraph{
    font-family: cursive;

}
.intro {
    width: 80%;
    margin-bottom: 5px;
}

@media (min-width: 768px) {
    .mainContent {
        margin: 0 auto;
        width: 75%;
        display: flex;
        justify-content: center;
        flex-direction: column;
        align-items: center;
        margin-top: 10px;
    }
    .message {
        display: flex;
        justify-content: center;
        /* align-items: center; */
        flex-direction: column;
        margin: 0 auto;
        width: 75%;
        max-width: 750px;
        box-shadow: 0.25rem 0.25rem 0.6rem rgba(0,0,0,0.05), 0 0.5rem 1.125rem rgba(75,0,0,0.05);
        border-radius: 2rem;
        font-size: 18px;
        padding: 2%;
        font-family: cursive;

    }
    .intro {
        width: 50%;
        margin-bottom: 5px;
    }
}


